import React from 'react';
import './App.css';
import { Nav, SmartSwitchBoard, Cart, Footer, CheckoutW, OrderStatus } from './widgets.js';
import bg from './final.jpg';
import { BrowserRouter, Route } from 'react-router-dom';




function App() {
  return (
    <BrowserRouter>
      <header>
        <Route path='' component={Nav} />
      </header>
      <Route exact path='/' component={SmartSwitchBoard} />
      <Route path='/cart' component={Cart} />
      <Route path='/checkout' component={CheckoutW} />
      <Route path='/orderstatus' component={OrderStatus} />
      <Route path='' component={Footer} />
    </BrowserRouter>
  );
}

export default App;
